                    <p style={{ textAlign: 'justify', textIndent: '2.5em' }}>In order to prevent traffic accidents, it is crucial to analyze the geographic and temporal characteristics of dangerous driving behavior. In this study, we analyze the potentially dangerous areas based on the Digital Tachograph (DTG) data together with the dangerous driving behavior criteria. Our findings showed that rapid acceleration (38.81%), sharp turn (24.72%), and overspeed (23.80%) are the top three most occurred dangerous driving behavior. Regarding the dangerous areas, our results revealed that there is a strong correlation between the dangerous driving behavior and the real traffic accidents which its correlation coefficient around 0.83. Within a week, Saturday (17.07%) shows the highest ratio of dangerous driving behavior occurrences. Within a day, between 6 AM to 8 AM (9.95%) show the highest ratio of dangerous driving behavior occurrences. By doing this, we conclude that our findings will be helpful to prevent future traffic accidents in both public transportation and private vehicles through traffic safety policy and traffic safety training for drivers.</p>
